import wasm from './wasm/wrap_tinyexr.wasm';

let tinyexr = null;
const getTinyexr = async () => {
    if (tinyexr) {
        return tinyexr;
    }

    tinyexr = await WebAssembly.instantiateStreaming(fetch(wasm));
    return tinyexr;
}

type EXR = {
    width: number;
    height: number;
    pixels: Float32Array;
};

export default async function parseExr(data: ArrayBuffer): Promise<EXR> {
    const { hey, malloc, memory } = await getTinyexr();
    console.log(hey, malloc, memory);
    // const input_ptr = malloc(data.byteLength);
    // console.log(malloc);
    return {
        width: 10,
        height: 10,
        pixels: new Float32Array()
    }
};