import * as React from 'react';
import { useState, useEffect } from 'react';
import { vec2, vec3 } from 'gl-matrix';
import { hsvToRgb, rgbToHsv } from '../color';
import Widget from './Widget';

import parseExr from '../loaders/exr';
import sonyStillLife from '../assets/aces-test/ACES/SonyF35.StillLife.exr'

const loadExr = async (background: string) => {
    const response = await fetch(background);
    const data = await response.arrayBuffer();
    return await parseExr(data);
};

export default function AcesTest() {
    useEffect(() => {
        loadExr(sonyStillLife).then(r => console.log('loadExr returned', r));
    }, []);
    return (
        <div style={{color: 'white', padding: '20px'}}>
            <h2>RTTODT</h2>
        </div>
    );
}
